import * as React from 'react';
import CookieConsent from 'react-cookie-consent';

import { HeaderDesktop } from './headerdesktop';
import { HeaderMobile } from './headermobile';
import { Footer } from './footer';

import { library } from '@fortawesome/fontawesome-svg-core';
import { faGoogle, faFacebook, faTwitter } from '@fortawesome/free-brands-svg-icons';
import {
  faSearch,
  faHome,
  faAngleRight,
  faPaperPlane,
  faEnvelope,
  faPhone,
  faClock,
  faChevronLeft,
  faCheck,
  faBuilding,
  faMapMarkerAlt,
  faCalculator,
  faShoppingCart,
  faFilePdf,
} from '@fortawesome/free-solid-svg-icons';
import Navigation from './navigation';

library.add(
  faSearch,
  faGoogle,
  faFacebook,
  faTwitter,
  faHome,
  faAngleRight,
  faPaperPlane,
  faEnvelope,
  faPhone,
  faClock,
  faChevronLeft,
  faCheck,
  faBuilding,
  faMapMarkerAlt,
  faCalculator,
  faShoppingCart,
  faFilePdf,
);

export const Layout: React.FC = ({ children }) => (
  <>
    <CookieConsent location="bottom" buttonText="Accept" declineButtonText="Decline" cookieName="gatsby-gdpr-google-analytics">
      This site uses cookies ...
    </CookieConsent>

    <HeaderDesktop />
    <Navigation />
    <HeaderMobile />
    {children}
    <Footer />
  </>
);
