import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { useIntl, FormattedMessage, Link } from 'gatsby-plugin-intl';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';

function renderAbout(locale: string) {
  switch (locale) {
    case 'fr':
      return <p>FOURNISSEURS DE SERVICE COMPLET POUR L'QUIPEMENT DE SUPPORT AU SOL</p>;
    case 'es':
      return <p>PROVEEDORES DE SERVICIO COMPLETO PARA EQUIPOS DE APOYO A TIERRA</p>;
    default:
      return <p>FULL-SERVICE PROVIDERS FOR GSE</p>;
  }
}

function renderSubscription(locale: string) {
  switch (locale) {
    case 'fr':
      return <p>Abonnez-vous  notre mailing</p>;
    case 'es':
      return <p>Suscrbete a nuestro correo</p>;
    default:
      return (
        <p>
          Stay up to date with the latest news on GSE.
          <br />
          Be the first to know about the latest additions to our range.
        </p>
      );
  }
}

export const Footer: React.FunctionComponent = () => {
  const intl = useIntl();

  return (
    <>
      <section id="newsletter">
        <Container>
          <Row className="justify-content-around">
            <Col lg="8" className="text-center">
              <Row>
                <Col md="1">
                  <FontAwesomeIcon icon="paper-plane" />
                </Col>
                <Col md="11">
                  <p className="text-left">{renderSubscription(intl.locale)}</p>
                </Col>
              </Row>
            </Col>
            <Col lg="4">
              <p>
                <FormattedMessage id="subscribe-to-our-mailing" />
              </p>
              <form
                className="js-cm-form"
                id="subForm"
                action="https://www.createsend.com/t/subscribeerror?description="
                method="post"
                data-id="2BE4EF332AA2E32596E38B640E905619E2B568366EE2B65A36680098EDFEA7C2A3C0B7F2AE57D8437596785229822E87C614D33CE90CD38C44857BED4C9B1363"
              >
                <div className="input-group">
                  <input
                    aria-label="Email"
                    className="form-control js-cm-email-input"
                    id="fieldEmail"
                    maxLength={200}
                    name="cm-gdlpl-gdlpl"
                    required
                    type="email"
                    placeholder={intl.formatMessage({ id: 'your-email-address' })}
                  />
                  <div className="input-group-append">
                    <button className="btn btn-secondary" type="submit">
                      <FormattedMessage id="subscribe" />
                    </button>
                  </div>
                </div>
              </form>
            </Col>
          </Row>
        </Container>
      </section>

      <footer id="colophon" role="contentinfo">
        <div className="container">
          <div className="row">
            <div className="col-md-6 col-sm-6 text-widget">
              <h4>{renderAbout(intl.locale)}</h4>
              <nav className="footer-nav">
                <ul className="fa-ul">
                  <li>
                    <FontAwesomeIcon icon="angle-right" />
                    <Link to="/about">
                      <FormattedMessage id="about-us" />
                    </Link>
                  </li>
                  <li>
                    <FontAwesomeIcon icon="angle-right" />
                    <Link to="/news">
                      <FormattedMessage id="blog" />
                    </Link>
                  </li>
                  <li>
                    <FontAwesomeIcon icon="angle-right" />
                    <Link to="/contact">
                      <FormattedMessage id="contact" />
                    </Link>
                  </li>
                </ul>
              </nav>
            </div>
            <div className="col-md-6 col-sm-6 text-widget">
              <h4>
                <FormattedMessage id="gse" />
              </h4>
              <nav className="footer-nav">
                <ul className="fa-ul">
                  <li>
                    <FontAwesomeIcon icon="angle-right" />
                    <Link to="/catalogue">
                      <FormattedMessage id="by-category" />
                    </Link>
                  </li>
                  <li>
                    <FontAwesomeIcon icon="angle-right" />
                    <Link to="/brands">
                      <FormattedMessage id="by-brand" />
                    </Link>
                  </li>
                  <li>
                    <FontAwesomeIcon icon="angle-right" />
                    <Link to="/offers">
                      <FormattedMessage id="full-aviaco-catalog" />
                    </Link>
                  </li>
                </ul>
              </nav>
            </div>
          </div>
        </div>
        <div className="site-footer">
          <div className="container">
            <div className="row">
              <div className="col-md-12 text-center">
                <FormattedMessage id="footer" />
              </div>
            </div>
          </div>
        </div>
      </footer>
    </>
  );
};
