import React from 'react';
import { Link } from 'gatsby-plugin-intl';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { FormattedMessage } from 'gatsby-plugin-intl';
import { Language } from './language';
import Navbar from 'react-bootstrap/Navbar';
import Nav from 'react-bootstrap/Nav';
import { Search } from './search';
import Container from 'react-bootstrap/Container';

const Navigation: React.FunctionComponent = () => (
  <>
    <Navbar bg="dark" variant="dark" sticky="top" expand="lg">
      <Container>
        <Navbar.Toggle aria-controls="basic-navbar-nav" />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="mr-auto">
            <Nav.Link as={Link} to="/">
              <FontAwesomeIcon icon="home" />
            </Nav.Link>
            <Nav.Link as={Link} to="/maintenance">
              <FormattedMessage id="maintenance" />
            </Nav.Link>
            <Nav.Link as={Link} to="/refurbished">
              <FormattedMessage id="refurbished" />
            </Nav.Link>
            <Nav.Link as={Link} to="/catalogue">
              <FormattedMessage id="catalogue" />
            </Nav.Link>
            <Nav.Link as={Link} to="/brands">
              <FormattedMessage id="brands" />
            </Nav.Link>
            <Nav.Link as={Link} to="/news">
              <FormattedMessage id="whats-new" />
            </Nav.Link>
            <Nav.Link as={Link} to="/offers">
              <FormattedMessage id="current-offers" />
            </Nav.Link>
            <Nav.Link as={Link} to="/about">
              <FormattedMessage id="about-us" />
            </Nav.Link>
            <Nav.Link as={Link} to="/contact">
              <FormattedMessage id="contact" />
            </Nav.Link>
          </Nav>
          <Nav>
            <Nav.Item>
              <Language />
            </Nav.Item>
          </Nav>
        </Navbar.Collapse>
      </Container>
    </Navbar>
  </>
);

export default Navigation;
