import React, { useContext } from 'react';
import { StoreContext } from '../store/Store';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { Link } from 'gatsby-plugin-intl';
import Badge from 'react-bootstrap/Badge';

export const Cart: React.FunctionComponent = () => {
  const {
    productIds: [products],
    serialisedItemIds: [serialisedItems],
  } = useContext(StoreContext);

  const count = products.length + serialisedItems.length;

  return (
    <Link to="/request" style={{ position: 'relative' }}>
      <FontAwesomeIcon icon="shopping-cart" />
      <Badge pill variant="danger" style={{ position: 'absolute', right: '-75%', top: '-25%' }}>
        {count}
      </Badge>
    </Link>
  );
};
